import Vue from 'vue'
import VueRouter from 'vue-router'

// 懒加载
const Home = () => import('views/home/Home')
const Customer = () => import('views/customer/Customer')
const Login = () => import('views/login/Login')
const Mine = () => import('views/mine/Mine')
const Product = () => import('views/product/Product')
const ProductDetail = () => import('views/productdetail/ProductDetail')
const CustomerDetail = () => import('views/customerdetail/CustomerDetail')
const AddProduct = () => import('views/addproduct/AddProduct')
const AddCustomer = () => import('views/addcustomer/AddCustomer')
const AddProductNeed = () => import('views/addproductneed/AddProductNeed')
const UpdateCustomer = () => import('views/updatecustomer/UpdateCustomer')
const UpdateProduct = () => import('views/updateproduct/UpdateProduct')
const RecordProduct = () => import('views/recordproduct/RecordProduct')
const Test = () => import('views/test/Test')
const Car = () => import('views/car/Car')
const CarList = () => import('views/carlist/CarList')


Vue.use(VueRouter)
const routes = [
  {
    path: '',
    redirect: '/home'
  },	
  { 
    path: '/home',
	name:'home',
    // 指定的组件
    component:Home,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'模拟聊天室'
	},
  },	
  { 
    path: '/mine',
	name:'mine',
    // 指定的组件
    component:Mine,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'mine'
	},
  },
  { 
    path: '/customer',
	name:'customer',
    // 指定的组件
    component:Customer,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'customer'
	},
  },
  { 
    path: '/login',
	name:'login',
    // 指定的组件
    component:Login,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'login'
	},
  },
  { 
    path: '/product',
	name:'product',
    // 指定的组件
    component:Product,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'product'
	},
  },
  { 
    path: '/productdetail',
	name:'productdetail',
    // 指定的组件
    component:ProductDetail,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'productdetail'
	},
  },
  { 
    path: '/addproduct',
	name:'addproduct',
    // 指定的组件
    component:AddProduct,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'addproduct'
	},
  },
  { 
    path: '/addcustomer',
	name:'addcustomer',
    // 指定的组件
    component:AddCustomer,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'addcustomer'
	},
  },
  { 
    path: '/customerdetail',
	name:'customerdetail',
    // 指定的组件
    component:CustomerDetail,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'customerdetail'
	},
  },
  { 
    path: '/addproductneed',
	name:'addproductneed',
    // 指定的组件
    component:AddProductNeed,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'addproductneed'
	},
  },
  { 
    path: '/test',
	name:'test',
    // 指定的组件
    component:Test,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'test'
	},
  },
  { 
    path: '/updatecustomer',
	name:'updatecustomer',
    // 指定的组件
    component:UpdateCustomer,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'updatecustomer'
	},
  },
  { 
    path: '/updateproduct',
	name:'updateproduct',
    // 指定的组件
    component:UpdateProduct,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'updateproduct'
	},
  },
  { 
    path: '/recordproduct',
	name:'recordproduct',
    // 指定的组件
    component:RecordProduct,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'recordproduct'
	},
  },
  { 
    path: '/car',
	name:'car',
    // 指定的组件
    component:Car,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'car'
	},
  },
  { 
    path: '/carlist',
	name:'carlist',
    // 指定的组件
    component:CarList,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'carlist'
	},
  },
]
const router = new VueRouter({
  routes,
  // url模式
  //mode:'history'
  mode:'hash'
})

export default router
