<template>
	<div class="app">
		<keep-alive exclude="AddCustomer,UpdateCustomer,Customer,UpdateProduct,Product,AddProduct,CarList">
			<router-view/>
		</keep-alive>
		<!--下面可以在封装-->	
		<van-tabbar v-model="active" class="tabbar">
			<van-tabbar-item :icon="item.icon1" v-for="(item,index) in tabbars" :key="index" @click="change(item.name)" >{{item.title}}</van-tabbar-item>
		</van-tabbar>
	</div>
</template>

<script>

import {Tabbar, TabbarItem} from 'vant'

export default {
  name: 'App',
  components: {
	[Tabbar.name]:Tabbar,
	[TabbarItem.name]:TabbarItem,
  },
  data(){
	return{
		active:0,
		tabbars:[
			{name:'home',title:'首页',icon1:'home-o',dizhi2:''},
			{name:'product',title:'产品',icon1:'search',dizhi2:''},
			{name:'customer',title:'客户',icon1:'friends-o',dizhi2:''},
			{name:'mine',title:'我的',icon1:'setting-o',dizhi2:''},
			
		],
	}
  },
  methods:{
	change(url){
		//console.log(url)
		this.$router.push(url).catch(res=>{res})
	}
  }
}
</script>

<style>
.tabbar{
	background:white;
}
.van-tabbar-item--active{
	-background:black;
}
