export default{
	//添加
	addMItem(state,item){
		item.num=1
		state.goodCarList.push(item)
	},
	//删除
	deleteMItem(state,index){
		state.goodCarList.splice(index,1)
	},
	//获取goods的数据
	getMGoods(state,payload){
		if(payload.length!==0){payload.forEach(item=>{item.checked=false;item.num=1})}		//给每个获取的数据的值添加一个checked和num属性
		state.recordObj.list.push(...payload)
	},
	//获取图片列表订单的数据
	getMCar(state,payload){
		if(payload.length!==0){payload.forEach(item=>{item.checked=false;})}		//给每个获取的数据的值添加一个checked和num属性}
		state.carListImg.push(...payload)
	},
	
	//删除购物车的列表同时修改列表的选中值
	deleteCarM(state,payload){
		let index=state.goodCarList.findIndex(item=>item["id"]==payload.id)	 	//找出当前点击那个对象处于数组哪个下下标
		state.goodCarList.splice(index,1)										//把该对象从数组移除
		
		let recordObj=state.recordObj
		let goodsIndex=recordObj.list.findIndex(item=>item["id"]==payload.id)	//获取当前数组在列表数组的下标
		
		//移除同时需要把recordproduct页面的数据的选中也一样改变
		recordObj.list[goodsIndex]["checked"]=false								//设置该对象
	}
}

