import {getGoods} from 'network/good'
import {getCar} from 'network/car.js'

export default{
	addItem(context,payload){
		//context.commit('addMItem',payload)	//执行mutations里的方法
		return new Promise((resolve)=>{
			//这里需要判断一下该数据是否已经存在该数组里面了
			let index=context.state.goodCarList.findIndex(item=>item.id===payload.id)
			if(!payload.checked && index===-1){
				context.commit('addMItem',payload)
				resolve('选中+1')
			}else{
				context.commit('deleteMItem',index)
				resolve('选中-1')
			}
		})
	},
	//获取goods的数据
	getGoods(context){
		let page=context.state.recordObj.page
		getGoods(page).then(res=>{
			context.commit('getMGoods',res)
		})
	},
	//获取car的数据
	getCar(context){
		getCar(context).then(res=>{
			context.commit('getMCar',res)
		})
	},
	
	//删除购物车时修改列表的选中状态
	deleteCarSelected(context,payload){
		return new Promise(resolve=>{
			//这里交由actiuons执行
			context.commit('deleteCarM',payload)
			//修改获取的数组列里面的checked	
			resolve('移除成功')
		})
	}
}

