import {request} from './request'

export function addGood(good) {
  return request({
	url:'/vuedata/addGood.php',
    params:{
		good
    }
  });
}

export function getGoods(page) {
  return request({
	url:'/vuedata/getGoods.php',
    params:{
		page
    }
  });
}

export function deleteGood(id){
  return request({
	url:'/vuedata/deleteGood.php',
    params:{
		id
    }
  });
}

export function deleteGoods(ids){
  return request({
	url:'/vuedata/deleteGoods.php',
    params:{
		ids
    }
  });
}



