exports.install=function(Vue,options){
	/** 
			 * 时间戳格式化函数 
			 * @param  {string} format    格式 
			 * @param  {int}    timestamp 要格式化的时间 默认为当前时间 
			 * @return {string}           格式化的时间字符串 
	*/	
		Vue.prototype.test=function(){
			console.log("测试一下"+options)
		}
		//数据是否为空
		Vue.prototype.$checkIsNull=function(str){
			if (str == null || str == "") return true;
		}
		
		// 只能输入数字[0-9]
		Vue.prototype.$isDigits=function(str) {
			if (str == null || str == "") return true;
			var reg = /^\d+$/;
			return reg.test(str);
		}
		// 只能输入数字[0-9]
		Vue.prototype.$isEnglish=function(str) {
			if (str == null || str == "") return true;
			var reg = /^[A-Za-z]+$/;
			return reg.test(str);
		}

		// 匹配汉字
		Vue.prototype.$isChinese=function(str) {
			if (str == null || str == "") return true;
			var reg = /^[\u4e00-\u9fa5]+$/;
			return reg.test(str);
		}
		// 匹配中文
		Vue.prototype.$isChineseChar=function(str) {
			if (str == null || str == "") return true;
			var reg = /^[\u0391-\uFFE5]+$/;
			return reg.test(str);
		}
}